import { mapMutations, mapState } from 'vuex';
import Api from '@/common/api';
import store from '@/store/index';
export default {
	data() {
		return {
			username: '15917045458',
			password: '15917045458',
			logining: false
		};
	},
	onLoad() {},

	methods: {
		...mapMutations(['login']),
		inputChange(e) {
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		navBack() {
			uni.switchTab({
				url: '/pages/index/index'
			});
		},
		toForgetPwd() {
			uni.navigateTo({
				url: '/pages/public/forgetPwd'
			});
		},
		toRegist() {
			uni.navigateTo({
				url: '/pages/public/reg'
			});
		},
		wxGetUserInfo: function(res) {
			if (!res.detail.iv) {
				uni.showToast({
					title: '您取消了授权,登录失败',
					icon: 'none'
				});
				return false;
			}
			const that = this;
			let raw= res.detail.rawData;
			let cloudID= res.detail.cloudID;
			let encryptedData= res.detail.encryptedData;
			let iv=res.detail.iv;
			let signature=res.detail.signature;
			uni.login({
				provider: 'weixin',
				success: async function(loginRes) {
					let params= {
							code: loginRes.code,
							userInfo:raw ,
							cloudID:cloudID ,
							encrypted_data: encryptedData,
							iv:iv ,
							type: 1,
							signature:signature 
						}
					let res= await Api.apiCall('post', Api.member.loginByWxApplet, params);
					that.loginSuccess(res)
				}
			});
		},
		async toLogin() {
			let phoneReg = /^1[1-9][0-9]\d{8}$/;
			try {
				if (this.username == '') {
					throw '请填写手机号';
				}
				if (!phoneReg.test(this.username)) {
					throw '手机号格式有误';
				}
				if (this.password == '') {
					throw '请填写密码';
				}
			} catch (err) {
				this.$api.msg(err);
				return;
			}
			this.logining = true;
			let params = { username: this.username, password: this.password };
			let data = await Api.apiCall('post', Api.member.login, params);
			this.logining = false;
			this.loginSuccess(data);
		},
		loginSuccess(data){
			if (data.code == 0) {
				let data1 = data.data;
				this.login(data1);
				uni.switchTab({
					url: '/pages/index/index'
				});
			} else {
				this.$api.msg(data.msg);
			}
			
		}
		
	}
};
